<template>
  <div class="task">
    <h3>{{ task.title }}</h3>
    <div class="icons">
      <i 
        class="material-icons" 
        @click="taskStore.deleteTask(task.id)">
        delete      </i>
      <i     class="material-icons"
        :class="{active: task.isFav}" 
    @click="taskStore.toggleFav(task.id)">
    <i class="bi bi-heart-fill" id="bobo"></i>
      </i>
    </div>
  </div>
</template>

<script>
  import { useTaskStore } from './components/taskStore.js'
  export default {
    props: ['task'],
    setup() {
      const taskStore = useTaskStore()
      return { taskStore }
    }
  }
</script>